<template>
  <div class="DetailsComponent">
    <div class="columns is-desktop is-vcentered">
      <div class="column is-half-desktop is-full-mobile is-full-tablet">
        <p>
          <b>SELECT {{type_fullName}}</b>
        </p>
      </div>
      <div class="column is-half-desktop is-full-mobile is-full-tablet">
        <div v-if="isDevice">
          <multiselect
            v-model="selectedDevices"
            style="z-index: 998;"
            placeholder="Select Devices"
            group-label="name"
            group-values="devices"
            label="name"
            track-by="id"
            :options="devicesGrouped"
            :multiple="true"
            :searchable="false"
            :loading="devices.length==0"
            :clear-on-select="false"
            :close-on-select="false"
            :showNoResults="false"
            :maxHeight="600"
            @input="selectedDeviceChanged"
          ></multiselect>
        </div>
        <div v-else></div>
      </div>
    </div>

    <div class="columns is-desktop">
      <div class="column is-one-of-quarter">
        <p>
          <b>SCHEDULES</b>
        </p>
      </div>
      <div class="column is-full-mobile" style="position: relative;">
        <v-calendar
          v-blur="deviceBookings===undefined"
          is-expanded
          :theme-styles="calendar_theme"
          :attributes="calendar_attributes"
          @update:fromPage="calendar_pagemoved"
          @dayclick="day_clicked"
        ></v-calendar>
        <hollow-dots-spinner
          is-valigned
          v-show="deviceBookings===undefined"
          :animation-duration="1000"
          :dot-size="20"
          :dots-num="3"
          color="#ff1d5e"
          style="position: absolute; left: 50%; top: 50%; transform: translate(-50%, -50%);"
        />
      </div>
    </div>
    <CalendarDetailComponent :bookings="selectedDateBookings" :day="selectedDay"></CalendarDetailComponent>
    <div class="columns is-desktop">
      <div class="column is-half">
        <label>
          <b>START DATE/TIME</b>
        </label>
      </div>
      <div class="column">
        <datetime v-model="start_date" type="datetime"></datetime>
      </div>
    </div>
    <div class="columns is-desktop">
      <div class="column is-half">
        <label>
          <b>END DATE/TIME</b>
        </label>
      </div>
      <div class="column">
        <datetime v-model="end_date" type="datetime"></datetime>
      </div>
    </div>
    <div class="columns is-desktop">
      <div class="column is-half">
        <label>
          <b>NAME</b>
        </label>
      </div>
      <div class="column">
        <input class="input is-info" type="text" placeholder="Name" />
      </div>
    </div>
    <div class="columns is-desktop">
      <div class="column is-half">
        <label>
          <b>REASON</b>
        </label>
      </div>
      <div class="column">
        <textarea class="textarea is-info" placeholder="Booking reason" rows="3"></textarea>
      </div>
    </div>
    <div class="levels is-desktop">
      <div class="level-left">
      </div>
      <div class="level-right">
        <button type="submit" class="button is-info normal" v-on:click="submit" >SUBMIT</button> 
      </div>
    </div>
  </div>
</template>
<script>
import Vue from "vue";
import axios from "axios";
import Multiselect from "vue-multiselect";
import VCalendar from "v-calendar";
import "v-calendar/lib/v-calendar.min.css";
import { HollowDotsSpinner } from "epic-spinners";
import CalendarDetailComponent from "./CalendarDetailComponent.vue";
import Datetime from 'vue-datetime'
import 'vue-datetime/dist/vue-datetime.min.css'


/* eslint-disable no-console */
Vue.use(VCalendar, {
  firstDayOfWeek: 2
});
Vue.use(Datetime)

Vue.use(require("vue-moment"));

export default {
  name: "DetailsComponent",
  mounted: function() {
    axios.all([this.getDevices(), this.getDeviceTypes()]).then(
      axios.spread((deviceResponse, deviceTypeResponse) => {
        this.devices = deviceResponse.data;
        this.deviceTypes = deviceTypeResponse.data;
      })
    );
    this.getDeviceBookings(this.$moment().year(), this.$moment().month() + 1);
  },
  props: {
    type: String
  },
  data() {
    const hSpacing = "15px";
    return {
      t: "initialized",
      selectedDevices: [],
      devices: [],
      deviceTypes: [],
      getDevices: () => {
        return axios.get("/devices");
      },
      getDeviceTypes: () => {
        return axios.get("/deviceTypes");
      },
      getDeviceBookings: (year, month) => {
        month = month - 1; // To make month range 0-11 (momentjs)
        let dt_format = "YYYY-MM-DDTHH:mm:ss";
        axios
          .get("/deviceBookings", {
            params: {
              datetime_from: this.$moment
                .utc([year, month])
                .startOf("month")
                .format(dt_format),
              datetime_to: this.$moment
                .utc([year, month])
                .endOf("month")
                .format(dt_format)
            }
          })
          .then(response => {
            this.deviceBookings = response.data;
          });
      },
      deviceBookings: undefined,
      scheduledDates: [],
      selectedDay: undefined,
      calendar_theme: {
        wrapper: {
          background: "linear-gradient(to bottom right, #ff5050, #ff66b3)",
          color: "#fafafa",
          border: "0",
          borderRadius: "5px",
          boxShadow:
            "0 4px 8px 0 rgba(0, 0, 0, 0.14), 0 6px 20px 0 rgba(0, 0, 0, 0.13)"
        },
        header: {
          padding: `20px ${hSpacing}`
        },
        headerHorizontalDivider: {
          borderTop: "solid rgba(255, 255, 255, 0.2) 1px",
          width: "80%"
        },
        weekdays: {
          padding: `20px ${hSpacing} 5px ${hSpacing}`
        },
        weeks: {
          padding: `0 ${hSpacing} ${hSpacing} ${hSpacing}`
        },
        dayContent: {
          fontSize: "0.9rem"
        },
        dayCellNotInMonth: {
          opacity: 0.2
        }
      },
      start_date: "",
      end_date: ""
    };
  },
  computed: {
    type_fullName: function() {
      if (this.type == "d") return "DEVICE";
      else if (this.type == "f") return "FACILITY";
      else return "NONE";
    },
    isDevice: function() {
      return this.type == "d";
    },
    selectedDeviceIds: function() {
      return this.selectedDevices.map(d => d.id);
    },
    devicesGrouped: function() {
      return this.deviceTypes.map(devtype => {
        devtype.devices = this.devices.filter(dev => {
          return devtype.id == dev.type_id;
        });
        return devtype;
      });
    },
    booking_filtered: function() {
      if (this.deviceBookings === undefined) return [];
      return this.deviceBookings.filter(
        booking =>
          booking.device_ids.filter(id => this.selectedDeviceIds.includes(id))
            .length > 0
      );
    },
    selectedDateBookings: function() {
      if (this.selectedDay === undefined) return [];
      let m = this.$moment
        .utc([
          this.selectedDay.year,
          this.selectedDay.month - 1,
          this.selectedDay.day
        ])
        .utcOffset(-9);
      return this.booking_filtered
        .filter(booking => {
          let s = this.$moment
            .utc(booking.datetime_from)
            .startOf("day")
            .utcOffset(-9);
          let e = this.$moment
            .utc(booking.datetime_to)
            .endOf("day")
            .utcOffset(-9);
          return s <= m && m <= e;
        })
        .map(booking => {
          booking.devices = booking.device_ids.map(device_id =>
            this.devices.find(device => device.id == device_id)
          );
          return booking;
        });
    },
    calendar_attributes: function() {
      return this.booking_filtered.map(booking => {
        return {
          dates: {
            start: this.$moment
              .utc(booking.datetime_from)
              .utcOffset(9, true)
              .toDate(),
            end: this.$moment
              .utc(booking.datetime_to)
              .utcOffset(9, true)
              .toDate()
          },
          dot: {
            backgroundColor: "#ffffff",
            opacity: 1
          }
        };
      });
    }
  },
  methods: {
    selectedDeviceChanged: function(value, id) {},
    calendar_pagemoved: function(page) {
      this.getDeviceBookings(page.year, page.month);
    },
    day_clicked: function(day) {
      this.selectedDay = day;
    },
    submit: function() {
      alert('Submit will be implemented! THANK YOU!')
    }
  },
  components: {
    Multiselect,
    HollowDotsSpinner,
    CalendarDetailComponent
  }
};
/* eslint-enable no-console */
</script>

<style scoped lang="scss">
</style>

<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>
