<template>
  <div id="calendar-detail">
    <div class="columns is-multiline">
      <div class="column is-full" :key="0" v-show="bookings===undefined || bookings.length == 0">
        <div class="notification is-info" v-if="!(day === undefined)">
          <p class="subtitle is-5">
            <b>No conflicts on {{ $moment.utc(day.date).utcOffset(9).format('YYYY/MM/DD') }}</b>
          </p>
        </div>
      </div>
      <div class="column is-full" :key="booking.id" v-for="booking in bookings">
        <div class="notification tag-notification is-warning">
          <p class="subtitle is-5">
            <strong>Booking conflict on {{ $moment.utc(day.date).utcOffset(9).format('YYYY/MM/DD') }}</strong>
          </p>
          <p class="subtitle is-6">Booking Information</p>
          <p
            class="subtitle is-6"
            style="font-size: 0.8rem;"
          >Period: {{ $moment.utc(booking.datetime_from).format('YYYY/MM/DD HH:mm') + ' ~ ' + $moment.utc(booking.datetime_to).format('YYYY/MM/DD HH:mm') }}
          </p>
          <div class="tags">
            <span
              v-for="device in booking.devices"
              :key="device.id"
              class="tag is-small is-info is-rounded"
            >{{ device.name }}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";

Vue.use(require("vue-moment"));

export default {
  name: "CalendarDetailComponent",
  props: {
    bookings: Array,
    day: Object
  }
};
</script>

<style lang="scss" scoped>
.tag-notification p {
  margin: 0;
  margin-bottom: 12px;
}
</style>
