<template>
  <div id="app">
    <header class="section" style="padding-top: 0;">
      <div class="level is-mobile">
        <div class="level-left">
          <p>
            <b>NOTICE</b>
          </p>
        </div>
        <div class="level-right">
          <p>SONY FS5 CAME BACK!</p>
        </div>
      </div>
    </header>
    <div class="columns is-desktop">
      <div class="column is-half-desktop">
        <section class="section title">
          <transition :duration="2000" appear appear-to-class="animated fadeInUp">
            <div class="columns is-mobile is-gapless">
              <div class="column is-vcentered">
                <p class="tltx">MIGYO</p>
                <p class="subtltx">BOOK ARTECH'S
                  <br>EQUIPMENTS QUICKLY
                </p>
              </div>
              <div class="column is-narrow circles" style="position: relative;">
                <div class="circle circle-1"></div>
                <div class="circle circle-2"></div>
                <div class="circle circle-3"></div>
              </div>
            </div>
          </transition>
        </section>
      </div>
      <div class="column is-half-desktop">
        <section class="section">
          <div class="level is-mobile is-gapless" style="margin-bottom:8px;">
            <div class="level-left is-one-quarter">
              <p>
                <b>Student ID</b>
              </p>
            </div>
            <div class="level-right">
              <input
                style="text-align:right;"
                class="in_sid"
                v-model="sid"
                size="10"
                placeholder="20XXXXXX"
                :readonly="sid_executing"
              >
            </div>
          </div>
          <div class="level is-mobile is-gapless">
            <div class="level-left"></div>
            <div class="level-right">
              <hollow-dots-spinner
                :animation-duration="1000"
                :dot-size="20"
                :dots-num="3"
                color="#ff1d5e"
                v-show="sid_executing"
              />
              <p
                class="msgerr msg"
                v-show="sid.length > 0 && !(parseInt(sid).toString() == sid && sid.length == 8)"
              >INVALID STUDENT ID</p>
              <p
                class="msgok msg"
                v-show="sid_authorized"
              >O&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;NO PENALTY</p>
              <p
                class="msgerr msg"
                v-show="sid_unauthorized"
              >x&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;PENALTY</p>
              <p class="msgneutral msg" v-show="sid.length == 0">ENTER STUDENT ID</p>
            </div>
          </div>
          <div v-if="sid_authorized" key="details">
            <div class="level is-mobile">
              <div class="level-left">
                <p>
                  <b>Type</b>
                </p>
              </div>
              <div class="level-right">
                <button
                  :class="['type-btn', 'bttn-primary', 'bttn-sm', { 'bttn-bordered': isTypeFacility||noType, 'bttn-simple': isTypeDevice}]"
                  v-on:click="book_type='d'"
                  style="margin-right: 10px;"
                >DEVICE</button>
                <button
                  :class="['type-btn', 'bttn-primary', 'bttn-sm', { 'bttn-bordered': isTypeDevice||noType, 'bttn-simple': isTypeFacility}]"
                  v-on:click="book_type='f'"
                >FACILITIES</button>
              </div>
            </div>
            <DetailsComponent v-if="isTypeDevice || isTypeFacility" :type="book_type"/>
          </div>
        </section>
      </div>
      <!-- <FabComponent></FabComponent> -->
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { HollowDotsSpinner } from "epic-spinners";
import DetailsComponent from "./components/DetailsComponent.vue";
import FabComponent from "./components/FabComponent.vue";

axios.defaults.baseURL = "https://migyo.herokuapp.com";

export default {
  name: "app",
  data() {
    return {
      sid: "",
      sid_axios: undefined,
      sid_cancelSource: axios.CancelToken.source(),
      sid_lastResponse: undefined,
      book_type: ""
    };
  },
  watch: {
    sid: function(val) {
      this.book_type = "";
      if (parseInt(val).toString() == val && val.length == 8) {
        this.sid_axios = axios.create();
        this.sid_axios
          .get("/login", {
            CancelToken: this.sid_cancelSource.token,
            params: {
              student_id: this.sid
            }
          })
          .then(response => {
            this.sid_lastResponse = response;
          })
          .catch(error => {
            this.sid_lastResponse = error.response;
          })
          .then(() => {
            this.sid_axios = undefined;
          });
      } else {
        this.sid_cancelSource.cancel();
        this.sid_lastResponse = undefined;
      }
    }
  },
  computed: {
    sid_executing: function() {
      return this.sid_axios != undefined;
    },
    sid_authorized: function() {
      return (
        this.sid_lastResponse != undefined &&
        this.sid_lastResponse.status == 200
      );
    },
    sid_unauthorized: function() {
      return (
        this.sid_lastResponse != undefined &&
        this.sid_lastResponse.status == 401
      );
    },
    isTypeDevice: function() {
      return this.book_type == "d";
    },
    isTypeFacility: function() {
      return this.book_type == "f";
    },
    noType: function() {
      return this.book_type != "d" && this.book_type != "f";
    }
  },
  components: {
    HollowDotsSpinner,
    DetailsComponent,
    FabComponent
  }
};
</script>

