<template>
    <fab
        :position="position"
        :bg-color="bgColor"
        :icon-size="icon_size"
        :main-icon="main_icon"
        :actions="fabActions"
    >
    </fab>
</template>


<script>
import fab from 'vue-fab'

export default {
    name: 'FabComponent',
    components: {
        fab
    },
    data() {
        return {
            bgColor: '#ffc107',
            position: 'bottom-right',
            icon_size: 'small',
            main_icon: 'menu',
            fabActions: [
                {
                    name: 'calendar',
                    icon: 'calendar_today'
                }, 
                {
                    name: 'list',
                    icon: 'list'
                }
            ]
        }
    }
    
}
</script>
